@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@400;700;900&display=swap');
@import url('https://cdn.jsdelivr.net/npm/halfmoon@1.1.1/css/halfmoon-variables.min.css');

:root {
  --heading-font-weight: 900;
  --sidebar-title-font-weight: 700;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Red Hat Display", Montserrat, "Century Gothic", sans-serif;
}

.universe-title {
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 4px;
}

.card-title {
  font-weight: 700;
}

#card-container {
  column-count: 3;
  column-gap: 0;
}

#card-container > * {
  border: var(--card-border-width) solid var(--lm-card-border-color);
  box-shadow: var(--lm-card-box-shadow);
  -webkit-column-break-inside: avoid;
  display: inline-block;
  width: 100% !important;
  justify-items: center;
}

@media (max-width: 1199px){
  #card-container {
    column-count: 2;
  }
}
@media (max-width: 991px){
  #card-container {
    column-count: 1;
  }
}

/* Style override for Instagram embed cards */
#card-container > blockquote.instagram-media {
  position:relative;
  padding:var(--content-and-card-spacing);
  color:var(--lm-card-text-color);
  background-color:var(--lm-card-bg-color);
  background-image:var(--lm-card-bg-image);
  border:var(--card-border-width) solid var(--lm-card-border-color) !important;
  -moz-box-shadow:var(--lm-card-box-shadow) !important;
  -webkit-box-shadow:var(--lm-card-box-shadow) !important;
  box-shadow:var(--lm-card-box-shadow) !important;
  border-radius: 0 !important;
  margin: 0 !important;
}

/* Support for Adaptive Cards */
#card-container > div.ac-adaptiveCard {
  position:relative;
  margin:var(--content-and-card-spacing);
  padding:var(--content-and-card-spacing);
  color:var(--lm-card-text-color);
  background-color:var(--lm-card-bg-color);
  background-image:var(--lm-card-bg-image);
  border:var(--card-border-width) solid var(--lm-card-border-color);
  -moz-box-shadow:var(--lm-card-box-shadow);
  -webkit-box-shadow:var(--lm-card-box-shadow);
  box-shadow:var(--lm-card-box-shadow)
}

.ac-pushButton {
  display:inline-block;
  height:var(--button-height);
  line-height:var(--button-line-height);
  padding:var(--button-padding);
  text-align:center;
  white-space:nowrap;
  cursor:pointer;
  color:var(--lm-button-text-color);
  background-color:var(--lm-button-bg-color);
  background-image:var(--lm-button-bg-image);
  border:var(--button-border-width) solid var(--lm-button-border-color);
  border-radius:var(--button-border-radius);
  -moz-box-shadow:var(--lm-button-box-shadow);
  -webkit-box-shadow:var(--lm-button-box-shadow);
  box-shadow:var(--lm-button-box-shadow)
}
.ac-pushButton:hover {
  text-decoration:none;
  color:var(--lm-button-text-color-hover);
  background-color:var(--lm-button-bg-color-hover);
  background-image:var(--lm-button-bg-image-hover);
  border-color:var(--lm-button-border-color-hover);
  -moz-box-shadow:var(--lm-button-box-shadow-hover);
  -webkit-box-shadow:var(--lm-button-box-shadow-hover);
  box-shadow:var(--lm-button-box-shadow-hover)
}
.ac-pushButton.active,
.ac-pushButton:focus {
